<script>
    export default {
        data(){
            return{
                todo: ''
            }
        },
        methods:{
            submitHandler(){
                if(!this.todo) return
                this.$store.commit('ADD_TODOS',{
                    content:this.todo,
                    complete:false
                })
                this.todo = ''
            }
        }
    }
</script>

<template src = "./template.html"></template>

<style>
.todoInput{
    margin-bottom: 30px;
}
.InputFiled{
    width: 300px;
    height: 50px;
    font-size: 30px;
    text-align: center;
}
</style>