import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const LS = {
  load() {
    return JSON.parse(localStorage.getItem('vue-todo') || '[]')
  },

  save(data) {
    localStorage.setItem('vue-todo', JSON.stringify(data))
  }
}


const filter = {
  all(todos) {
    return todos
  },
  active(todos) {
    return todos.filter(todo => {
      return !todo.complete
    })
  },
  complete(todos) {
    return todos.filter(todo => {
      return todo.complete
    })
  }
}


export default new Vuex.Store({
  // strict: true, //嚴格模式確保dom結構正常，記得開發完成要取消以節省效能
  state: {
    todos: [
      {content:'todo-content1', complete: false},
      {content:'todo-content2', complete: true},
      {content:'todo-content3', complete: false},
    ]
  },

  getters:{
    todoIndex(state) {
      return filter[state.route.name](state.todos).map(item => state.todos.indexOf(item))
    }
    //製作一個INDEX陣列且確保是原始的
  },

  mutations: {
    SET_TODOS(state, data){
      state.todos = data
      // 儲存 LS
      LS.save(state.todos)
    },
    ADD_TODOS(state, data){
      state.todos.push(data)
      LS.save(state.todos)
    },
    UPDATE_TODOS(state, { index, data }){
      state.todos[index] = data
      LS.save(state.todos)
    },
    REMOVE_TODOS(state, index){
      state.todos.splice(index, 1)
      LS.save(state.todos)
    }
  },
  actions: {
    INIT_TODOS({ commit }){
      //讀取Local store，就是瀏覽器裡面的佔存

      commit('SET_TODOS', LS.load())
    }
  },
  modules: {
  }
})
