<script>
export default {
    props:{
        index:{
            type: Number,
            required: true
        }
    },
    computed:{
        todo(){
            return this.$store.state.todos[this.index]
        },
        complete: {
            get(){
                 return this.todo.complete
             },
            set(val){
                this.$store.commit('UPDATE_TODOS',{
                    index:this.index,
                    data: {
                        content: this.todo.content,
                        complete: val
                    }
                })
            }
        }
    },
    methods:{
        destroyHandler() {
            if(confirm(`是否確認刪除 ${this.todo.content} ?` ))
            this.$store.commit('REMOVE_TODOS',this.index)
        }
    }
}
</script>

<template src='./template.html'>   
</template>

<style>
.todoListItem{
    position: relative;
    background-color: #fff;
    display: flex;
    width: 500px;
    height: 50px;
    border: 1px #aaa;
    border-radius: 15px;
    margin-bottom: 20px;
    font-size: 20px;
    text-align: left;
    align-items: center;
    justify-content: left;
    padding-left: 10px;
}

.checkLabel{
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 22px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.checkLabel input{
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}
.checkmark{
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
}

.checkLabel:hover input ~ .checkmark{
    background-color: #ccc;
}

.checkLabel input:checked ~ .checkmark{
    background-color: #2196F3;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.checkLabel input:checked ~ .checkmark:after {
  display: block;
}

.checkLabel .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.destroy{
    position: absolute;
    right: 15px;
    background-color: tomato;
}
</style>